import React, {Component} from "react";
import { Container,Header,Button, Card, Image,Form, Table, Flag } from 'semantic-ui-react';
import axios from "axios";

const flagRenderer = (item) => <Flag name={item} />;
class Covid extends Component{
    constructor(props){
        super(props);
        this.state = {data:[]}
    }
    
    componentDidMount() {
        axios({
            "method":"GET",
            "url":"https://covid-19-data.p.rapidapi.com/country/all",
            "headers":{
            "content-type":"application/octet-stream",
            "x-rapidapi-host":"covid-19-data.p.rapidapi.com",
            "x-rapidapi-key":"713cd8f77bmsh9a5002439da72bbp1d520fjsnd2b28b8cfaab",
            "useQueryString":true
            },"params":{
            "format":"json"
            }
            })
            .then((response)=>{
              console.log(response)
            })
            .catch((error)=>{
              console.log(error)
            })
      }
  handleChange = (e, { value }) => this.setState({ value }) 
    render(){
        return (
            <Container>
                <Header as='h2' icon='plug' content='Exam' />
                
                <Card.Group>
                    <Card fluid>
                    <Card.Content>
                        <Card.Header>Covid-19 Stats bY Country</Card.Header>
                        {/* <Card.Meta>Friends of Elliot</Card.Meta> */}
                        <Card.Description>
                            {this.state.data}
                        <Table>
                            <Table.Header>
                            <Table.Row>
                                {/* <Table.HeaderCell>Flag</Table.HeaderCell> */}
                                <Table.HeaderCell>Country</Table.HeaderCell>
                                <Table.HeaderCell>confirmed</Table.HeaderCell>
                                <Table.HeaderCell>critical</Table.HeaderCell>
                                <Table.HeaderCell>recovered</Table.HeaderCell>
                                <Table.HeaderCell>deaths</Table.HeaderCell>
                            </Table.Row>
                            </Table.Header>
                            <Table.Body>
                            {this.state.data.map((res) => (
                                <Table.Row key={res.country}>
                                {/* <Table.Cell>{flagRenderer(res.country)}</Table.Cell> */}
                                <Table.Cell>{res.country}</Table.Cell>
                                <Table.Cell>{res.confirmed}</Table.Cell>
                                <Table.Cell>{res.critical}</Table.Cell>
                                <Table.Cell>{res.recovered}</Table.Cell>
                                <Table.Cell>{res.deaths}</Table.Cell>
                                </Table.Row>
                            ))}
                            </Table.Body>
                        </Table>
                        </Card.Description>
                    </Card.Content>
                    <Card.Content extra>
                        <div className='ui two buttons'>
                        <Button basic color="blue">
                            Previous
                        </Button>
                        
                        <Button basic color="blue">
                            Next
                        </Button>
                        </div>
                    </Card.Content>
                    </Card>
                </Card.Group>
                
            
            </Container>
        )
    }
}

export default Covid;